// Saves options to chrome.storage
const saveOptions = () => {
    const enable_plugin = document.getElementById("enable_plugin").checked;
    const enable_fix = document.getElementById("enable_fix_phrase").checked;

    chrome.storage.sync.set(
        { enable_plugin: enable_plugin, enable_fix: enable_fix },
        () => {
            const status = document.getElementById('status');
            status.textContent = 'Настройки сохранены';
            setTimeout(() => {
                status.textContent = '';
            }, 750);
        }
    );
};


const restoreOptions = () => {
    chrome.storage.sync.get(
        { enable_plugin: true, enable_fix: true },
        (items) => {
            console.log('restoreOptions',items)
            document.getElementById("enable_plugin").checked = items.enable_plugin;
            document.getElementById("enable_fix_phrase").checked = items.enable_fix;
        }
    );
};

document.addEventListener('DOMContentLoaded', restoreOptions);
document.getElementById('save').addEventListener('click', saveOptions);


document.getElementById("link_enable_fix").addEventListener("click", () => {
    document.getElementById('fix_enable_block').style.display = 'block';
})
